.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}


a {
    padding: 20px;
}

.twitter-tweet {
    margin: auto;
}

.approval {
    background-color: lightgray;
    width: 400px;
    margin: auto;
    padding: 30px;
}

.invisible {
    display: none;
}

button {
    margin: 10px;
}

.back {
    display: block;
}

select {
    margin: 10px;
}

.amount {
    width: 50px;
}


.connect {
    background-color: lightblue;
}

.tweet-input {
    width: 409px;
    height: 40px;
    font-size: 20px;
}
